import { BpmnModel } from "../BpmnModel";
import { BpmnModelHelper } from "../BpmnModelHelper";
import { MexExpression } from "./MexExpression";

export class ConstantExpression extends MexExpression {
    constructor(model: BpmnModel) {
        super(model);
    }

    readonly ClrTypeID: string = "bpmn.Mex.ConstantExpression";
    NewInstance(): ConstantExpression {
        return new ConstantExpression(this.Model);
    }

    Value: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.Value = null;
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "Value", this.Value);

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        this.Value = obj.Value;
    }
}